
html {
  font-size: 62.5%;
}
body {
  background: #F6F6F6;
}
#app {
  max-width: 41.2rem;
  min-height: 41.2rem;
  margin: 0 auto;
  background: #fff;
}
.has-header {
  margin-top: 4.8rem;
}
h2 {
  font-size: 1.68rem;
  font-weight: normal;
  color: #111;
}
ul, li, a, h2 {
  margin: 0;
  padding: 0;
}
li {
  list-style: none;
}
a {
  text-decoration: none;
}
.header-bar {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 998;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 4.8rem;
  padding: 0 1.8rem;
  background: #fff;
}
.gray-btn-sm {
  padding: 5px 15px;
  color: #8590a6;
  text-align: center;
  border-color: transparent;
  border-radius: 3px;
  box-sizing: border-box;
  background: #f6f6f6;
}
.gray-btn-md {
  cursor: pointer;
  width: 3.6rem;
  padding: 0.8rem 1.4rem;
  font-size: 1.6rem;
  text-align: center;
  color: #8590a6;
  background: #f6f6f6;
  border: .1rem solid #f6f6f6;
  border-radius: .3rem;
}
.darkgray-btn-sm {
  cursor: pointer;
  width: 3.6rem;
  padding: 0.8rem 1.4rem;
  font-size: 1.6rem;
  color: #fff;
  text-align: center;
  border-color: transparent;
  box-sizing: border-box;
  background: #8590A6;
}
.darkgray-btn-md {
  cursor: pointer;
  width: 3.6rem;
  padding: 0.8rem 1.4rem;
  font-size: 1.6rem;
  text-align: center;
  color: #fff;
  background: #8590A6;
  border: .1rem solid #8590A6;
  border-radius: .3rem;
}
.blue-btn-sm {
  padding: 5px 15px;
  text-align: center;
  border-color: transparent;
  border-radius: 3px;
  box-sizing: border-box;
  background: rgba(0, 132, 255, 0.15);
  color: #0084ff;
}
.blue-btn-md {
  cursor: pointer;
  width: 3.6rem;
  padding: 0.8rem 1.4rem;
  font-size: 1.6rem;
  text-align: center;
  color: #0084ff;
  background: rgba(0, 132, 255, 0.15);
  border: 0.1rem solid rgba(0, 132, 255, 0.15);
  border-radius: .3rem;
}
.navy-btn-md {
  cursor: pointer;
  width: 3.6rem;
  padding: 0.8rem 1.4rem;
  font-size: 1.6rem;
  text-align: center;
  color: #fff;
  background: #337ab7;
  border: .1rem solid #337ab7;
  border-radius: .3rem;
}
.navy-btn-lg {
  cursor: pointer;
  width: 100%;
  padding: 1.2rem 2.6rem;
  margin-top: 1rem;
  font-size: 1.7rem;
  text-align: center;
  color: #fff;
  background: #337ab7;
  border: .1rem solid #337ab7;
  border-radius: .3rem;
}
.cl-dkg {
  color: darkgray;
}
.theme-blue {
  color: #36538b !important;
}
.filter-gray {
  filter: grayscale(100%);
}
.ipt-wrapper {
  position: relative;
  display: -ms-flexbox;
  display: flex;
  width: 180px;
  height: 34px;
  padding: 4px 10px;
  font-size: 14px;
  background: #FFFFFF;
  border: 1px solid #EBEBEB;
  border-radius: 3px;
  box-sizing: border-box;
  margin: 8px auto;
}
.main-ipt {
  -ms-flex: 1;
      flex: 1;
  padding: 0;
  overflow: hidden;
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background: transparent;
  border: none;
  outline: none;
  resize: none;
}
.tip {
  font-size: 1.4rem;
  color: #aaa;
  text-align: center;
}
.msgError {
  color: #ff0000;
}
.msgSuccess {
  color: #3ba94d;
}
.modal-wrapper {
  width: 100%;
  height: 100%;
  position: fixed;
  overflow: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  -webkit-overflow-scrolling: touch;
  outline: 0;
  background: rgba(55, 55, 55, 0.6);
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.modal-dialog {
  width: 80%;
  max-width: 400px;
  background: #fff;
  padding: 60px 20px;
  text-align: center;
}
.modal-btn {
  margin-top: 3rem;
}
.msgDialog {
  background: #f6f6f6;
  top: -200px;
  text-align: center;
  padding: 10px;
  position: absolute;
  z-index: 9999;
  width: 300px;
  left: 50%;
  margin-left: -160px;
  animation: msgBounce 1.5s ease;
}
.msgDialogErr {
  background: #f6f6f6;
  top: -200px;
  text-align: center;
  padding: 10px;
  position: absolute;
  z-index: 9999;
  width: 300px;
  left: 50%;
  margin-left: -160px;
  animation: msgBounceErr 1.5s ease;
}
@keyframes msgBounce {
0% {
    top: -200px;
    opacity: 1;
}
94% {
    top: 94px;
    opacity: 1;
}
98% {
    top: 98px;
    opacity: 0;
}
100% {
    top: -100px;
    opacity: 0;
}
}
@keyframes msgBounceErr {
0% {
    top: -200px;
    opacity: 1;
}
94% {
    top: 94px;
    opacity: 1;
}
98% {
    top: 98px;
    opacity: 0;
}
100% {
    top: -100px;
    opacity: 0;
}
}



